.section {
    margin-bottom: 10px;
}

.section__header {
    display: flex;
    justify-content: space-between;
    background-color: white;
    margin: 0;
    margin-bottom: 2px;
    padding: 20px;
}

@media (max-width: 700px) {
    .section__header {
        flex-direction: column;
    }
}